import ProjectItem from './ProjectItem';

const ProjectList = () => {
  const projectList = [
    {
      name: 'HKUST - Admissions',
      image: 'images/projects/hkust-undergraduate-admissions.jpg',
      link: 'https://join.hkust.edu.hk/',
      languages: 'Drupal, Vue, JavaScript, LESS, jQuery',
    },
    {
      name: 'HKUST - Study Abroad',
      image: 'images/projects/hkust-studyabroad.jpg',
      link: '',
      languages: 'Drupal, Vue, JavaScript, LESS, jQuery',
    },
    {
      name: 'HKUST - 30A',
      image: 'images/projects/hkust-30a.jpg',
      link: 'https://30a.hkust.edu.hk/',
      languages: 'Drupal, React, JavaScript, LESS',
    },
    {
      name: 'HKUST - Strategic Plan',
      image: 'images/projects/hkust-strategicplan.jpg',
      link: 'https://strategicplan.hkust.edu.hk/',
      languages: 'Drupal, LESS, JavaScript, jQuery',
    },
    {
      name: 'ACIM CityU',
      image: 'images/projects/cityu-acim.jpg',
      link: 'https://www.acim.cityu.edu.hk/',
      languages: 'Drupal, JavaScript, LESS, jQuery',
    },
    {
      name: 'HKU - Admissions',
      image: 'images/projects/hku-admissions.jpg',
      link: 'https://admissions.hku.hk/',
      languages: 'Drupal, Vue, JavaScript, LESS, jQuery',
    },
  ];

  return (
    <div className="project-items">
      {projectList.map((project, idx) => (
        <ProjectItem
          key={idx}
          name={project.name}
          image={project.image}
          link={project.link}
          languages={project.languages}
        />
      ))}
    </div>
  );
};

export default ProjectList;
