import {
  faGlobeAmericas,
  faLaptopCode,
} from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

const ProjectItem = (props) => {
  return (
    <div className="project-item">
      <div className="project-image">
        {props.image.length > 0 && (
          <img src={props.image} alt={props.name} width={800} height={450} />
        )}
      </div>
      <div className="project-content">
        <div className="project-content__name">{props.name}</div>
        <div className="project-content__link">
          {props.link.length > 0 && (
            <a
              className="project-link"
              href={props.link}
              target="_blank"
              rel="noreferrer"
            >
              <FontAwesomeIcon icon={faGlobeAmericas} className="icon" />
            </a>
          )}
          {props.link.length === 0 && (
            <div className="no-result">Website not published yet</div>
          )}
        </div>
        <div className="project-content__languages">
          <FontAwesomeIcon icon={faLaptopCode} className="icon" />{' '}
          {props.languages}
        </div>
      </div>
    </div>
  );
};

export default ProjectItem;
