const MainHero = (props) => {
  const Background = props.bgImage;

  return (
    <header
      className={`main-hero ${props.isHome === true ? 'is-home' : ''}`}
      style={{ backgroundImage: 'url(' + Background + ')' }}
    >
      <div className="main-hero-text">
        <div className="container">
          <h1 className="main-hero-title">{props.title}</h1>
          {props.subTitle ? (
            <p className="main-hero-subtitle">{props.subTitle}</p>
          ) : null}
        </div>
      </div>
    </header>
  );
};

export default MainHero;
