import React, { useEffect } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faLinkedin, faGithubSquare } from '@fortawesome/free-brands-svg-icons';

const TheNavbar = () => {
  useEffect(() => {
    trackScrollUpOrDown();
  }, []);

  const trackScrollUpOrDown = () => {
    const navBar = document.querySelector('.main-navigation');
    let pastYOffset = 0;

    window.addEventListener('scroll', () => {
      const newYOffset =
        window.pageYOffset || document.documentElement.scrollTop;
      newYOffset > 0
        ? navBar.classList.add('is-scroll')
        : navBar.classList.remove('is-scroll');
      newYOffset > pastYOffset
        ? navBar.classList.add('scrolling')
        : navBar.classList.remove('scrolling');
      // For Mobile or negative scrolling
      pastYOffset = newYOffset <= 0 ? 0 : newYOffset;
    });
  };

  return (
    <>
      <nav className="main-navigation">
        <div className="container nav-container">
          <div className="nav-logo">Jeffrey Tiu</div>
          <div className="nav-icon">
            <a
              href="https://www.linkedin.com/in/jeffreytiu/"
              target="_blank"
              rel="noreferrer"
              alt="Jeffrey Linkedin"
            >
              <FontAwesomeIcon icon={faLinkedin} />
            </a>
            &nbsp;&nbsp;
            <a
              href="https://github.com/jeffreytiu"
              target="_blank"
              rel="noreferrer"
              alt="Jeffrey GitHub"
            >
              <FontAwesomeIcon icon={faGithubSquare} />
            </a>
          </div>
        </div>
      </nav>
    </>
  );
};

export default TheNavbar;
