import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faLinkedin, faGithubSquare } from '@fortawesome/free-brands-svg-icons';

const TheFooter = () => {
  return (
    <>
      <footer id="copyright">
        <div className=" container text-center">
          <a
            href="https://www.linkedin.com/in/jeffreytiu/"
            target="_blank"
            rel="noreferrer"
            alt="Jeffrey Linkedin"
          >
            <FontAwesomeIcon icon={faLinkedin} />
          </a>
          &nbsp;&nbsp;
          <a
            href="https://github.com/jeffreytiu"
            target="_blank"
            rel="noreferrer"
            alt="Jeffrey GitHub"
          >
            <FontAwesomeIcon icon={faGithubSquare} />
          </a>
          <p>
            Designed and code by Jeffrey Tiu © 2021
            <br />
            All rights reserved.
          </p>
        </div>
      </footer>
    </>
  );
};

export default TheFooter;
